export default {
  data() {
    return {
      resetLoading: false
    };
  },
  methods: {
    async handleReset(key, value) {
      try {
        this.resetLoading = true;
        this.$delete(this.query, key, value);
        await this.handleGet();
        this.resetLoading = false;
      } catch (e) {
        this.$message.error(e.response.data.message);
      }
    }
  }
};
