export default {
  data() {
    return {
      loading: false
    };
  },
  methods: {
    async handleGet() {
      this.loading = true;
      try {
        await this.get(this.query);
        this.loading = false;
      } catch (e) {
        this.$message.error(e.response.data.message);
      }
    }
  }
};
