<template>
  <div class="filter-container">
    <el-row type="flex">
      <el-col :span="12">
        <el-form
          ref="searchForm"
          :rules="rules"
          :model="search"
          inline
          @keydown.enter.native="handleSearch"
        >
          <el-form-item prop="key">
            <el-select placeholder="请选择字段" v-model="search.key">
              <el-option
                v-for="(searchColumn, index) in searchColumns"
                :label="searchColumn.title"
                :value="searchColumn.name"
                :key="index"
              />
            </el-select>
          </el-form-item>

          <el-form-item prop="value">
            <el-input
              v-model="search.value"
              clearable
              placeholder="请输入内容"
            ></el-input>
          </el-form-item>

          <el-form-item>
            <el-button
              v-if="searchAble"
              @click.native="handleSearch"
              :loading="searchLoading"
              icon="el-icon-search"
              type="primary"
              >查询</el-button
            >
            <el-button
              v-if="resetAble"
              @click.native="handleReset"
              :loading="resetLoading"
              icon="el-icon-refresh"
              >重置</el-button
            >
          </el-form-item>
        </el-form>
      </el-col>

      <el-col :span="12" class="tools">
        <!-- <el-button
          v-if="exportAble"
          :loading="exportLoading"
          type="primary"
          icon="el-icon-download"
          >导出</el-button
        > -->
        <el-button
          v-if="addAble"
          type="success"
          icon="el-icon-edit"
          @click.native="handleAdd"
          >新增</el-button
        >
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "TableTool",
  data() {
    return {
      search: {
        key: "",
        value: ""
      },
      rules: {
        key: [{ required: true, message: "请选择筛选字段", trigger: "blur" }],
        value: [{ required: true, message: "请输入筛选内容", trigger: "blur" }]
      }
    };
  },
  props: {
    searchColumns: {
      type: Array,
      required: true
    },
    searchAble: {
      type: Boolean,
      default: true
    },
    resetAble: {
      type: Boolean,
      default: true
    },
    addAble: {
      type: Boolean,
      default: false
    },
    exportAble: {
      type: Boolean,
      default: false
    },
    searchLoading: {
      type: Boolean,
      default: false
    },
    resetLoading: {
      type: Boolean,
      default: false
    },
    exportLoading: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    handleSearch() {
      this.$refs.searchForm.validate(vaild => {
        if (vaild) {
          this.$emit("on-search", this.search.key, this.search.value);
        }
      });
    },
    async handleReset() {
      await this.$emit("on-reset", this.search.key, this.search.value);
      this.$refs.searchForm.resetFields();
    },
    handleAdd() {
      this.$emit("on-add", true);
    }
  }
};
</script>

<style lang="scss">
.tools {
  text-align: right;
}
</style>
