export default {
  data() {
    return {
      searchLoading: false
    };
  },
  methods: {
    async handleSearch(key, value) {
      try {
        this.searchLoading = true;
        this.$set(this.query, key, value);
        await this.handleGet();
        this.searchLoading = false;
      } catch (e) {
        this.$message.error(e.response.data.message);
      }
    }
  }
};
